.slider {
  position: relative;
  width: 100%;
}

.main-image {
  width: 100%;
}

.buttons {
  position: absolute;
  top: 50%;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

#prev,
#next {
  border: none;
  color:darkcyan;
  background-color: transparent;
  padding: 1rem 0.5rem;
}

#prev:hover,
#next:hover {
  border: none;
  background:darkcyan;
  color: white;
  padding: 1rem 0.5rem;
}

/*Slider with gallery*/
.gallery {
  margin: 1rem 0;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.gallery img {
  width: 100px;
}
.image-selected {
  border: 3px solid darkcyan;
}

/*Slider with dots*/
.dots{
    position:absolute;
    bottom:5%;
    display: flex;
    justify-content:center;
    align-items: center;
    width: 100%;
}

.dot{
    width:10px;
    height:10px;
    background-color: transparent;
    border: 1px solid white;
    border-radius: 50%;
    margin:5px;
}

.selected{
    width:15px;
    height:15px;
    background-color: white;
    border-radius: 50%;
    margin:5px;
}